<template>
	<div id="main">
	  <popup :showModel="showModel" @clear="clear()"></popup>
	  <div id="container">
		<div id="left">
			<div id="icon">
				<router-link to="/"><i class="fa fa-home" aria-hidden="true" /></router-link>
				<router-link to="/alarm" id="selected"><i class="fa fa-warning" aria-hidden="true" /></router-link>
				<router-link to="/traffic"><i class="fa fa-street-view" aria-hidden="true" /></router-link>
			</div>
		</div>
		<div id="right">
		  <div id="title">
			<div id="word">&ensp;<i class="fa fa-warning" aria-hidden="true" />&ensp;警报处理</div>
		  </div>
		  <div id="list">
			<table class="table table-bordered table-striped table-hover">
			  <thead>
				<tr>
				  <th>编号</th>
				  <th>警报时间</th>
				  <th>警报类型</th>
				  <th>警报区域</th>
				  <th>警报描述</th>
				  <th>摄像头id</th>
				  <th>状态</th>
				  <th>操作</th>
				</tr>
			  </thead>
			  <tbody>
				<tr v-for="i in content">
				  <td>{{ i.id }}</td>
				  <td>{{ i.warn_time }}</td>
				  <td>{{ i.warn_type }}</td>
				  <td>{{ i.warn_area }}</td>
				  <td>{{ i.warn_description }}</td>
				  <td>{{ i.camera_id }}</td>
				  <td>{{ i.info }}</td>
				  <td><button type="button" class="btn btn-success" @click="update(i.id)">处理</button></td>
				</tr>
			  </tbody>
			</table>
		  </div>
		</div>
	  </div>
	</div>
</template>
  
<script>
	import popup from "./Popup.vue"
	export default{
		components:{
			popup,
    	},
		data(){
			return{
				content:null,
				showModel:false,
			}
		},
		mounted(){
			this.refresh()
		},
		methods:{
			refresh(){
				this.axios.get('http://localhost:8000/api/warn/')
				.then(res=>{
					this.content=res.data.result
				})
			},
			update(id){
				this.showModel=true;
				this.axios.get('http://localhost:8000/api/warn/',{
					params:{
						id:id,
					}
				})
				.then(res=>{
					this.content=res.data.result
				})
			},
			clear(){
				this.showModel=false
				this.refresh()
			}
		}
	}
</script>

<style scoped>
#main{
	width: 100vw;
	height: 100vh;
	box-sizing: border-box;
}
#container{
	width: 100%;
	height: 90%;
	position: relative;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	background-color: #fff;
	box-shadow: 0 0 3px rgb(57,167,176),
				0 0 6px rgb(56,183,145);
	display: flex;
}
#left{
	flex: 1;
	background-color: #eeeeee;
	box-shadow: inset -15px 0px 10px -10px #666666;
}
#left #icon{
	font-size: 50px;
	text-align: center;
	position: relative;
	left: 50%;
	transform: translate(-50%);
}
#right{
	flex: 20;
	display: flex;
	flex-direction: column;
}
#title{
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-image: linear-gradient(135deg,rgb(57,167,176),rgb(56,183,145));
}
#word{
	font-size: 3em;
	font-weight: lighter;
	color: #fff;
}
#list{
	flex: 5;
	overflow-x: hidden;
	overflow-y: scroll;
	padding: 2%;
}
a{
	color: #666666;
	transition: all 0.2s;
}
a:hover{
	color: #0088ff;
	transition: all 0.2s;
}
#selected{
	color: #0088ff;
}
</style>